*
{
  margin: 0;
  padding: 0;
}
.container {
  background:  url(todo\ list.webp);
    background-size: cover;
    background-repeat: no-repeat;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-wrapper{
  background-color: dimgray;
  min-width: 450px;
  min-height: 500px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}
.header h1
{
  color: #fff;
  text-align: center;
  margin: 25px 0;

}
.task-input
{
  outline: none;
  width: 250px;
  padding: 15px;
  margin-right: 10px;
  margin-left: 15px;
  color: #ccc;
  background-color: #000000;
  border: 1px solid #c89666;
  border-radius: 10px;
}
.button-add
{
  width: 70px;
  padding: 12px;
  font-size: 18px;
  border-radius: 10px;
  border: 1px solid black;
  background-color: #f1af71;
  cursor: pointer;
}
.list-item
{
  display: flex;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  max-height: 30px;

}
.button-delete
.button-edit
{
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}
.button-edit
{
  background-color: aqua;
  padding: 9px 18px;
  font-size: 15px;
  border-radius: 5px;
  font-weight: bolder;
  border: none;
  outline: none;
  cursor: pointer;
}
.button-delete
{
  background-color: blue;
  padding: 9px 18px;
  margin-left: 5px;
  font-size: 15px;
  border-radius: 5px;
  font-weight: bolder;
  color: whitesmoke;
  border: none;
  outline: none;
  cursor: pointer;
}
.list
{
  width: 220px;
  background-color: #12343b;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
  border-radius: 5px;
  list-style-type: none;
}
.todo-list
{
  list-style-type: none;
  margin-left: 30px;
  margin-top: 10px;
  display: flex;
  background-color: transparent;
  margin-left: 25px;

}